<template>
  <div id="ashow-blog">
    <el-row :gutter="20">
        <el-col :span="15" :offset="4">
            <el-input class="box-card" v-model="search">
                <el-button slot="append" icon="search" @click="searchBlog"></el-button>
            </el-input>
        </el-col>
        <el-col :span="16" :offset="4">
            <el-card v-for="blog in blogs" class="box-card">
                <router-link :to="{name:'article',params:{id:blog.id}}">
                <!-- <router-link :to="'article/'+blog.id"> -->
                    <h2>{{blog.title}}</h2>
                </router-link>
                <article>
                    {{blog.content|snippet}}
                </article>
            </el-card>
        </el-col>
    </el-row>
  </div>
</template>

<script>

export default {
  name: 'show-blog',
  data () {
    return {
        blogs:[],
        search:''
    }
  },
  created () {
    // this.$http.get('https://jsonplaceholder.typicode.com/posts')
    // .then(function(data){
    //     this.blogs=data.body.slice(0,6);
    // })
    this.$http.get('https://vue-demo-9ff1b.firebaseio.com/blogs.json')
    .then(function(data){
      return data.json()
    })
    .then(function(data){
      var blogArray=[];
      for(let key in data){
        data[key].id =key;
        blogArray.push(data[key]);
      }
      this.blogs =blogArray;
    })
  },
  methods:{
    searchBlog () {
        this.blogs =this.blogs.filter((blog) => {
            return blog.title.match(this.search);
        })
    }
  }
}
</script>

<style scoped>
.box-card{
    margin:15px;
}
a{
  text-decoration: none;
}
</style>


