<template>
  <div id="article">
    <el-col :span="16" :offset="4">
      <el-card>
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
      </el-card>

    </el-col>

  </div>
</template>

<script>
export default {
  name: 'article',
  data () {
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created () {
    this.$http.get('https://vue-demo-9ff1b.firebaseio.com/blogs/'+this.id+".json")
    .then(function(data){
      return data.json()
    })
    .then(function(data){
      this.blog = data
    })
  }
}
</script>

<style scoped>

</style>


