<template>
  <div id="add-blog">
    <el-row :gutter="20">
        <el-col :span="16" :offset="4">
            <el-form label-width="80px" v-if="!submitted">
                <el-form-item label="主题">
                    <el-input type="text" v-model="blog.title"></el-input>
                </el-form-item>
                <el-form-item label="内容">
                    <el-input :rows="6" type="textarea" v-model="blog.content"></el-input>
                </el-form-item>
                <el-form-item label="分类">
                    <el-checkbox-group v-model="blog.checkList">
                        <el-checkbox label="Vue.js"></el-checkbox>
                        <el-checkbox label="Angular4"></el-checkbox>
                        <el-checkbox label="Node.js"></el-checkbox>
                        <el-checkbox label="React.js"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="作者">
                    <el-select v-model="blog.selectValue">
                        <el-option value="kichizo">kichizo</el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">立即添加</el-button>
                </el-form-item>
            </el-form>
            <div v-else>
                已发布新博客
            </div>
        </el-col>
    </el-row>

  </div>
</template>

<script>


export default {
  name: 'add-blog',
  data () {
    return {
        blog:{
            title:'',
            content:'',
            checkList:[],
            selectValue:''
        },
        submitted:false
    }
  },
  methods:{
    onSubmit () {
        this.$http.post("https://vue-demo-9ff1b.firebaseio.com/blogs.json",this.blog)
        .then(function(data){
            this.submitted =true;
        })
    }
  }
}
</script>

<style scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
}
.el-col {
    border-radius: 4px;
}
</style>


